class Task {
    // todo delete å†…å­˜idå¢åŠ å™¨ï¼ˆå› ä¸ºmockåˆå§‹åŒ–å¤ªå¿«äº†ï¼Œéƒ½åœ¨ä¸€ä¸ªæ¯«ç§’å†…ï¼‰
    private static idCounter = 0

    public id: number = -1

    public title: string = "undefine"
    public note: Note = new Note()

    public level: number = 3

    public createTime: number = -1
    public doneTime: number = -1

    // å¯ä¿®æ”¹ï¼Œæœ€åä¸€ä¸ªæœ‰æ•ˆ
    public deadlines: number[] = []
    // é¢„è®¡èŠ±è´¹æ—¶é—´ï¼Œå•ä½æ¯«ç§’ï¼Œå¯ä¿®æ”¹ï¼Œæœ€åä¸€ä¸ªæœ‰æ•ˆ
    public expectConsumes: number[] = []

    @Type(() => Duration)
    public doingDurs: Duration[] = []

    // ååºåˆ—åŒ–ä¸èƒ½æœ‰å¸¦å‚æ•°çš„æ„é€ å™¨
    // public constructor(title: string) {
    //     this.title = title
    //     this.createTime = Utils.getTimestamp()

    //     this.id = Utils.generateId() + Task.idCounter
    //     Task.idCounter = Task.idCounter + 1000
    // }

    public init(title: string) {
        this.title = title
        this.createTime = Utils.getTimestamp()

        this.id = Utils.generateId() + Task.idCounter
        Task.idCounter = Task.idCounter + 1000
    }

    public isSameTask(task: Task | undefined): boolean {
        if (task === undefined) {
            return false
        } else {
            return this.id === task.id
        }
    }

    public isDoing(): boolean {
        if (this.doingDurs.length === 0) {
            return false
        } else {
            let lastTask = this.doingDurs[this.doingDurs.length - 1]
            return lastTask.end === -1
        }
    }

    public lastStartTime(): number {
        if (this.doingDurs.length === 0) {
            return -1
        } else {
            let lastTask = this.doingDurs[this.doingDurs.length - 1]
            return lastTask.start
        }
    }

    public start() {
        let duration = new Duration()
        duration.init(Utils.getTimestamp())
        this.doingDurs.push(duration)
    }

    public stop() {
        this.doingDurs[this.doingDurs.length - 1].end = Utils.getTimestamp()
    }

    public stopDelay(delay:number) {
        this.doingDurs[this.doingDurs.length - 1].end = Utils.getTimestamp()+ delay
    }

    public isDone(): boolean {
        return this.doneTime !== -1
    }

    public done() {
        this.doneTime = Utils.getTimestamp()
    }

    // è¿”å›æ¯«ç§’æ•°
    // todo rename getAllDoingDuration
    public getAllDuration(): number {
        if (this.doingDurs.length === 0) {
            return 0
        }

        return this.doingDurs.map((value, index) => value.getDur(index === this.doingDurs.length - 1)).reduce((a, b) => a + b)
    }

    public getActualDeadline(): number {
        if (this.deadlines.length === 0) {
            return -1
        }

        return this.deadlines[this.deadlines.length - 1]
    }

    public getActualExpectConsume(): number {
        if (this.expectConsumes.length === 0) {
            return -1
        }

        return this.expectConsumes[this.expectConsumes.length - 1]
    }

    // è¶…å‡ºé¢„æœŸæ—¶é—´è¿”å›å¤šèŠ±çš„æ—¶é—´ï¼Œè´Ÿå€¼
    public getLeftExpectConsumeTime(): number {
        let left = this.getActualExpectConsume() - this.getAllDuration()
        if (left < 0) {
            console.error("è¶…å‡ºé¢„æœŸæ—¶é—´")
        }
        return left
    }
}

class Duration {
    public start: number = -1
    public end: number = -1

    // todo æ²¡æ³•é‡è½½
    public init(start: number) {
        this.start = start
    }
    public init2(start: number, end?: number) {
        this.start = start
        if (end != undefined) {
            this.end = end
        }
    }

    public isIn(timeStamp: number) {
        return timeStamp >= this.start && timeStamp <= this.end
    }

    public getDur(isDoing: boolean): number {
        let end = this.end
        if (end == -1 && isDoing) {
            end = Utils.getTimestamp()
        }
        if (end == -1) {
            console.error("è¿™æ®µæ—¶é—´æœªç»“æŸ")
            return 0
        }
        return end - this.start
    }

    public isValid(isDoing: boolean) {
        if (!isDoing && this.end == -1) return false
        return true
    }
}

class Note {
    public content: string = ""
}

class StoreModel {
    @Type(() => Task)
    public tasks: Task[] = []

    /**
     * ç­›é€‰è§„åˆ™ï¼š
     * 1. æ‰€æœ‰æœªå®Œæˆçš„ä»»åŠ¡ï¼ˆä¹‹åè¦æ’é™¤éå½“å¤©çš„è®¡åˆ’ä»»åŠ¡ï¼‰
     * 2. ä»Šå¤©å®Œæˆçš„ä»»åŠ¡
     */
    public getTasks(dur: Duration): Task[] {
        return this.tasks.filter((value) => { return !value.isDone() || dur.isIn(value.doneTime) })
    }

    public addTask(task: Task) {
        if (this.containsProject(task.id)) return

        this.tasks.push(task)
    }

    public removeTask(task: Task): boolean {
        if (!this.containsProject(task.id)) return false

        let length = this.tasks.length;
        for (let i = 0; i < length; i++) {
            if (this.tasks[i].id === task.id) {
                if (i === 0) {
                    this.tasks.shift(); //åˆ é™¤å¹¶è¿”å›æ•°ç»„çš„ç¬¬ä¸€ä¸ªå…ƒç´ 
                } else if (i === length - 1) {
                    this.tasks.pop();  //åˆ é™¤å¹¶è¿”å›æ•°ç»„çš„æœ€åä¸€ä¸ªå…ƒç´ 
                } else {
                    this.tasks.splice(i, 1); //åˆ é™¤ä¸‹æ ‡ä¸ºiçš„å…ƒç´ 
                }
                return true
            }
        }
        return false
    }

    private containsProject(id: number): boolean {
        return this.tasks.find(item => item.id === id) !== undefined
    }

    public orderTasks() {
        this.tasks.sort((a, b) => {
            if (a.isDone() && !b.isDone()) {
                return 1
            } else if (!a.isDone() && b.isDone()) {
                return -1
            } else {
                if (a.level > b.level) {
                    return 1
                } else if (a.level < b.level) {
                    return -1
                } else {
                    if (a.createTime > b.createTime) {
                        return 1
                    } else if (a.createTime < b.createTime) {
                        return -1
                    } else {
                        return 0
                    }
                }
            }
        })
    }
}


export { Task, Duration, StoreModel, Note }